











































import { VNode, CreateElement } from 'vue';
import { Component, Vue, Prop } from 'vue-property-decorator';
import anime from 'animejs';

interface Coordinate {
  x: number;
  y: number;
}

@Component
export default class Background extends Vue {
  coordinates: Coordinate[] = [];

  created() {
    setInterval(() => {
      if (this.coordinates.length !== 0) {
        this.coordinates.splice(0);
      }
    }, 1000);
    this.$nextTick(() => {
      anime({
        targets: '.ring',
        scale: 1.5,
        easing: 'easeOutSine',
        borderColor: 'rgba(0, 0, 0, 0)',
        duration: 2000,
        autoplay: true,
        loop: true,
      });
    });
  }

  render(h: CreateElement): VNode {
    const contNode: VNode[] = [];
    for (const el of this.coordinates) {
      contNode.push(this.getBlock(h, el.x, el.y));
    }
    return (
      <div
        class='anime-container'
        on-mousemove={(event: MouseEvent) => {
          this.onMouseMove(event);
        }}>
        <div class='solid-block'></div>
        <div class='ring'></div>
        {contNode}
      </div>
    ) as VNode;
  }

  updated() {
    this.setAnime();
  }

  getBlock(h: CreateElement, x: number, y: number): VNode {
    const computedStyle = {
      left: `${x}px`,
      top: `${y}px`,
    };
    return (<div class='block' style={computedStyle}></div>) as VNode;
  }

  onMouseMove(event: MouseEvent) {
    this.coordinates.push({
      x: event.clientX,
      y: event.clientY,
    });
  }

  private setAnime() {
    anime({
      targets: '.block',
      scale: 0,
      easing: 'easeOutSine',
      duration: 500,
    });
  }
}
